import { property } from '@websublime/wbs-ui-common/utils/decorators';
import {
  ComponentMetadata,
  ComponentMixinInterface,
  Constructor,
  WebComponentOptions
} from '@websublime/wbs-ui-common/utils/types';
import { LitElement, ReactiveElement } from 'lit';

/**
 * @public
 */
export function ComponentMixin<T extends Constructor<ReactiveElement>>(
  constructor: T
): T & Constructor<ComponentMixinInterface> {
  class SuperElement extends constructor {
    // TODO: merge options from defineWebComponent
    @property({ type: Object })
    options!: WebComponentOptions;

    /**
     * @private
     */
    @property({ reflect: true })
    public dir: 'ltr' | 'rtl' = 'ltr';

    /**
     * @private
     */
    public get isLTR(): boolean {
      return this.dir === 'ltr';
    }
  }

  return SuperElement;
}

/**
 * @public
 */
export class ComponentElement extends ComponentMixin<
  Constructor<ReactiveElement>
>(LitElement) {
  public registry: ComponentMetadata;

  constructor(registry: ComponentMetadata) {
    super();

    this.registry = Object.freeze(registry);
  }
}

/**
 * Register a custom element Lit class component
 *
 * @public
 */
export function defineWebComponent<WebComponent extends ComponentElement>(
  name: string,
  component: new () => WebComponent,
  options?: WebComponentOptions
): new () => WebComponent {
  Object.defineProperty(component.prototype, 'options', {
    enumerable: true,
    value: options
  });

  window.customElements.define(name, component);

  return component;
}
